import React, { useState, useRef, useEffect } from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import { TextArea  } from "carbon-components-react";
import { EventAPI } from "../../../commons/EventAPI";

const dispatchedEvents = [EventAPI.TEXTAREA_CHANGED];

/*
    Example: <srs-textarea label="Label" rows="3" cols="150"/>
*/
const SrsTextArea = function (props, ref) {
    const [inputValue, setInputValue] = useState(props.value || "");
    useEffect(() => {
        setInputValue(props.value);
        props.setValue(props.value);
    }, [props]);

    let inlineStyles = {
        "--cds-label-01-font-size": "0.9rem",
        "--cds-label-01-font-weight": 800,
    };

    return (
        <div style={inlineStyles}>
            <TextArea
                id={props.elementid}
                labelText={props.label}
                rows={props.rows}
                cols={props.cols}
                value={inputValue}
                disabled={props.disabled}
                placeholder={props.placeholder}
                readOnly={props.readonly}
                onBlur={(evt) => {
                    if(props.onChange) {
                        props.onChange(evt.target?.value || "");     
                    }
                }}
                onChange={(evt) => {
                    setInputValue(evt.target?.value || "");
                    props.dispatchEvent(EventAPI.TEXTAREA_CHANGED, { payload: {value: evt.target?.value || "", id: props.elementid} });
                    props.setValue(evt.target?.value || "");
                }}/>
        </div>
      );
}

const styles = `
    .cds--text-area {
        border: 1px solid lightgray !important;
        border-radius: 5px;
    }

    .cds--text-area:focus, .cds--text-area:active {
        outline: 0px !important;
        border: 2px solid !important;
        border-radius: 5px;
    } 
`
const props = [{
    name: "label",
    defaultValue: "Label",
    type: "string"
}, {
    name: "rows",
    defaultValue: 4,
    type: "number"
}, {
    name: "cols",
    defaultValue: 100,
    type: "number"
}, {
    name: "value",
    defaultValue: "",
    type: "string"
}, {
    name: "disabled",
    defaultValue: false,
    type: "boolean"
}, {
    name: "placeholder",
    defaultValue: "Enter value...",
    type: "string"
}, {
    name: "readonly",
    defaultValue: false,
    type: "boolean"
}];

const slots = [];

const WrappedComponent = wrapper(React.forwardRef(SrsTextArea), props);
export { WrappedComponent, styles, props, dispatchedEvents, slots };